* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button:focus, fieldset:focus, input:focus {outline:0;}

fieldset {
  border: 0 none;
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}


@font-face {
  font-family: Calibre Web;
  src: url(../../static/media/CalibreWeb-Regular.25ea1155.ttf);
  font-weight: 400;
}

@font-face {
  font-family: Calibre Web;
  src: url(../../static/media/CalibreWeb-Semibold.d36e8e99.ttf);
  font-weight: 600;
}

body, h1, p, button {
  background-color: #f5f7fa;
  font-family: Calibre Web;
  font-weight: 400;
  color: #40484d;
  -webkit-font-smoothing: antialiased;
}

button {
    background: 0 0;
    border: 0 none;
    border-radius: 0;
    text-transform: none;
    line-height: normal;
    margin: 0;
    cursor: pointer;
}

.icon {
  color: #a2a4a6;
  cursor: pointer;
}

.icon:hover {
  color: #40484d;
}

.primary-header {
  position: fixed;
  height: 65px;
  width: 100%;
  border-bottom: 1px solid #e0e5eb;
  top: 0;
  left: 0;
  padding: 0;
  background-color: #fff;
  z-index: 10;
}

.right-header {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
}

.page-title {
  font-size: 21px;
  display: inline-block;
  background-color: #fff;
  margin-top: 18px;
  padding-left: 22px;
  cursor: pointer;
}

.page-title span:first-child:hover {
  background-color: #f5f7fa;
}

.nav-item-dash {
  font-size: 29px;
  padding: 5px 18px 0 18px;
  display: inline-block;
  border-right: 1px solid #e0e5eb;
  color: #a2a4a6;
  height: 65px;
  text-align: center;
  float: left;
  fill: #a2a4a6;
}

.nav-item-right-1 {
  font-size: 15px;
  padding: 5px 25px 0 25px;
  display: inline-block;
  color: #a2a4a6;
  height: 65px;
  text-align: center;
  float: left;
  fill: #a2a4a6;
  border-right: 1px solid #e0e5eb;
}

.nav-item-right-2 {
  font-size: 19px;
  padding: 5px 22px 0 22px;
  display: inline-block;
  color: #a2a4a6;
  height: 65px;
  text-align: center;
  float: left;
  fill: #a2a4a6;
}

.nav-right-account {
  position: relative;
  display: inline-block;
  border-right: 1px solid #e0e5eb;
  padding: 18px 38px 0 18px;
  cursor: pointer;
  height: 65px;
  text-align: center;
  float: left;
}

.nav-right-account:hover .account-dropdown {
  display: block;
}

/* Arrow Icon */
.nav-item-right-3 {
  font-size: 10px;
  color: #a2a4a6;
  position: absolute;
  top: 43%;
  right: 18px;
  padding: 0;
}

.nav-item-dash:hover, .nav-item-right-1:hover, .nav-item-right-2:hover, .nav-item-right-3:hover {
  color: #40484d;
}

.account-dropdown {
  position: absolute;
  display: none;
  right: -1px;
  width: 170px;
  text-align: left;
  font-size: 18px;
  line-height: 18px;
  background-color: #fff;
  margin: 0;
  border: 1px solid #e0e5eb;
  border-top: 0;
  top: 100%;
  padding: 10px 0;
  cursor: default;
}

.account-dropdown li {
  padding: 6px 20px;
}

.account-dropdown li:hover {
  color: #38dedf;
}

.account-dropdown li a {
  cursor: pointer;
  color: #40484d;
  text-decoration: none;
}
.account-dropdown li a:hover {
  color: #38dedf;
}

.account-dropdown-secondary {
  font-size: 16px;
  color: #869096;
}
.account-dropdown-secondary:hover {
  color: #869096 !important;
}

.isfirst {
  border-top: 1px dotted #e0e5eb;
  margin-top: 6px;
  padding-top: 12px !important;
}
.account-dropdown-linkbold a {
  text-decoration: none;
  font-weight: 600;
  color: #38dedf !important;
}
.account-dropdown-linkbold a:hover {
  color: #20c3c4 !important;
}

/* FILTER BAR */

.filter-bar {
  display: block;
  color: #869096;
  padding-top: 40px;
  margin-left: 40px;
  margin-right: 40px;
}

.filter-bar-icons {
  color: #869096;
  font-size: 14px;
  margin: -2px 8px 0 -5px;
  padding: 7px;
}

.lock-text span {
  font-size: 16px;
  padding-left: 6px;
}

.filter-bar-icons:hover {
  color: #20c3c4;
}

.filter-drop-down {
  padding: 3px 7px 5px 7px;
  border: 1px solid #d0d2d5;
  color: #869096;
  border-radius: 2px;
  display: inline-block;
}

.filter-drop-down button {
  font-size: 10px;
  color: #869096;
  padding-left: 14px;
}

.img-post {
  width: 16.6%;
  float: left;
  padding: 20px;
  text-align: center;
  position: relative;
  display: block;
}

.img-post:hover .more-container, .img-post:hover .check-icon {
  display: block;
}

.img-container {
  background-color: #e4e9f2;
  height: 0;
  padding-top: 100%;
  display: block;
  position: relative;
}

.more-container {
  position: absolute;
  bottom: 10px;
  width: 100%;
  display: none;
}

.more-btn {
  font-size: 26px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #fff;
  color: #40484d;
  padding-top: 2px;
}

.more-btn:hover {
  color: #fff;
  background-color: #40484d;
}

.more-btn-on {
  font-size: 26px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #20c3c4;
  color: #fff;
  padding-top: 2px;
}

.options-icons {
  color:#fff;
  font-size: 21px;
  text-align: left;
  display: block;
  margin-top: 5px;
}

.options-icons span {
  color: #c2cdd1;
  font-size: 16px;
  position: absolute;
  margin: 2px 0 0 5px;
}

.options-icons span:hover {
  text-decoration: underline;
}

#options-dropdown {
  display: none;
  position: absolute;
  top: 40px;
  width: 176px;
  background-color: #202325;
  box-shadow: 0 1px 0 rgba(0,0,0,.3);
  padding: 10px;
  border-radius: 2px;
  margin-left: -88px;
  left: 50%;
}

#options-dropdown:before {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #202325 transparent;
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: -10px;
  margin-left: -10px;
}

.post-name {
  margin-top: 10px;
  padding: 5px 8px;
  background: #fff;
  max-width: 100%;
  text-align: center;
  height: 23px;
  font-size: 16px;
  color: #869096;
  border-width: 0;
  border-radius: 2px;
  display: inline-block;
}

.check-icon {
  display: none;
  font-size: 11px;
  position: absolute;
  z-index: 10;
  outline: 0;
  width: 28px;
  height: 28px;
  border: 2px solid rgba(255,255,255,.75);
  color: rgba(255,255,255,.75);
  border-radius: 50%;
  left: 20px;
  top: 20px;
  margin: 10px 0 0 10px;
  background: rgba(15,18,21,.4);
}

.check-icon:hover {
  border-color: #fff;
  color: #fff;
}

.color-bar {
  width: 25%;
  height: 100%;
  display: inline-block;
}

.background-overlay {
  position: fixed;
  visibility: visible;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 120;
  text-align: center;
  overflow-y: auto;
  background-color: rgba(32,35,37,.85);
  transition: all 250ms ease-in-out;
  -webkit-transition: all 250ms ease-in-out;
}

.background-overlay-hide {
  position: fixed;
  visibility: hidden;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 120;
  text-align: center;
  overflow-y: auto;
  background-color: rgba(32,35,37,0);
  transition: all 250ms ease-in-out;
  -webkit-transition: all 250ms ease-in-out;
}

.background-overlay:before, .background-overlay-hide:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.add-file-container {
  opacity: 0;
  position: relative;
  max-width: 430px;
  width: 90%;
  text-align: left;
  margin: 90px 0px;
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
}
.add-file-container-animin {
  -webkit-animation: displayLightbox .5s ease;
  -webkit-animation-fill-mode: forwards;
  -moz-animation: displayLightbox .5s ease;
  -moz-animation-fill-mode: forwards;
  -ms-animation: displayLightbox .5s ease;
  -ms-animation-fill-mode: forwards;
  animation: displayLightbox .5s ease;
  animation-fill-mode: forwards;
}

.add-file-container-animout {
  -webkit-animation: hideLightbox .7s ease;
  -webkit-animation-fill-mode: forwards;
  -moz-animation: hideLightbox .7s ease;
  -moz-animation-fill-mode: forwards;
  -ms-animation: hideLightbox .7s ease;
  -ms-animation-fill-mode: forwards;
  animation: hideLightbox .7s ease;
  animation-fill-mode: forwards;
}

.add-file-window {
  border-radius: 2px 2px 0px 0px;
  padding: 40px 40px 30px 40px;
  display: block;
  background-color: #fff;
  overflow: hidden;
}

.add-file-button-group {
  padding-bottom: 16px;
  margin-bottom: 20px;
  position: relative;
  border-bottom: 1px dotted rgb(208, 210, 213);
  overflow: hidden;
}

.add-selector-button {
  font-size: 18px;
  background-color: #e4e9f2;
  display: inline-block;
  color: #869096;
  width: 48%;
  float: left;
  text-align: center;
  margin: 0px 1% 2% 1%;
  padding: 9px 0px;
  border-radius: 2px;
  text-decoration: none;
  overflow: hidden;
}

.add-selector-button-full {
  width: 98%;
}

.add-selector-button-full:before {
  font-size: 20px !important;
  margin-top: 1px !important;
}

.add-selector-button:before {
  position: absolute;
  margin-left: -5px;
  margin-top: 1px;
}

.add-selector-button span {
  font-size: 18px;
  padding-left: 17px;
}

.add-selector-button-off {
  background-color: #e4e9f2;
  color: #869096;
}

.add-selector-button-on {
  background-color: #31383c;
  color: #fff;
  font-size: 14px;
}

.add-selector-button-on:before {
  margin-top: 3px;
}

fieldset {
  margin-bottom: 15px;
}

.text-field {
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.08) inset;
  background-color: #f5f7fa;
  font-size: 16px;
  padding: 11px;
  width: 100%;
  border: 1px solid transparent;
  border-radius: 2px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.text-field::-webkit-input-placeholder {
  color: #a7abb8;
}

.text-field::-ms-input-placeholder {
  color: #a7abb8;
}

.text-field::placeholder {
  color: #a7abb8;
}

.text-field:focus {
  background-color: #fff;
  box-shadow: none;
  border: 1px solid #38dedf;
}

.upload-note {
  font-size: 14px;
  color: #aeb3c0;
  display: block;
  margin-top: 10px;
  line-height: 1.4;
}

.add-file-button {
  display: block;
  width: 100%;
  padding: 16px 10px 20px;
  background-color: #38dedf;
  color: #fff;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  border: 0 none;
}

.add-file-button:hover {
  background-color: #20c3c4;
}

.add-file-button::before {
  font-size:13px;
}

.add-file-button span {
  padding-left: 8px;
}

.add-file-button-loading {
  display: block;
  width: 100%;
  padding: 16px 10px 20px;
  background-color: #20c3c4;
  color: #fff;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  border: 0 none;
}

.add-file-button-loading::before {
  display: inline-block;
  font-size:13px;
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
}

.close-button {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.75);
  position: absolute;
  right: -45px;
  top: -15px;
  padding: 15px;
  cursor: pointer;
}

.close-button:hover {
  opacity: .75;
}

/* Responsive Styles */
@media only screen and (max-width: 1350px) {
  .img-post {
    width: 25%;
  }
}
@media only screen and (max-width: 1000px) {
  .img-post {
    width: 33.33333%;
  }
}
@media only screen and (max-width: 630px) {
  .img-post {
    width: 50%;
  }
  .img-post:focus .more-container, .img-post:focus .check-icon {
    display: block;
  }
  .filter-bar, .nav-item-dash, .nav-item-right-2 {
    display: none;
  }
  .close-button {
    font-size: 20px;
    margin-left: 50%;
    left: -10px;
    right: auto;
    top: -50px;
    padding: 0;
    z-index: 20;
  }
}

/* animations */

@-webkit-keyframes displayLightboxOverlay {
    from {
        background-color: rgba(32, 35, 37, 0)
    }
    to {
        background-color: rgba(32, 35, 37, .85)
    }
}

@keyframes displayLightboxOverlay {
    from {
        background-color: rgba(32, 35, 37, 0)
    }
    to {
        background-color: rgba(32, 35, 37, .85)
    }
}

@-webkit-keyframes displayLightbox {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, 50px, 0);
        transform: translate3d(0, 50px, 0)
    }
    100% {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0)
    }
}

@keyframes displayLightbox {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, 50px, 0);
        transform: translate3d(0, 50px, 0)
    }
    100% {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0)
    }
}

@-webkit-keyframes hideLightbox {
    0% {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0)
    }
    100% {
        opacity: 0;
        -webkit-transform: translate3d(0, -200px, 0);
        transform: translate3d(0, -200px, 0)
    }
}

@keyframes hideLightbox {
    0% {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0)
    }
    100% {
        opacity: 0;
        -webkit-transform: translate3d(0, -200px, 0);
        transform: translate3d(0, -200px, 0)
    }
}

@-webkit-keyframes hideLightbox {
    0% {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0)
    }
    100% {
        opacity: 0;
        -webkit-transform: translate3d(0, -200px, 0);
        transform: translate3d(0, -200px, 0)
    }
}

@keyframes hideLightbox {
    0% {
        opacity: 1;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0)
    }
    100% {
        opacity: 0;
        -webkit-transform: translate3d(0, -200px, 0);
        transform: translate3d(0, -200px, 0)
    }
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.color-grab {
  width: 200px;
  height: 200px;
}

