@charset "UTF-8";

@font-face {
  font-family: "dm-icons";
  src:url("fonts/dm-icons.eot");
  src:url("fonts/dm-icons.eot?#iefix") format("embedded-opentype"),
    url("fonts/dm-icons.woff") format("woff"),
    url("fonts/dm-icons.ttf") format("truetype"),
    url("fonts/dm-icons.svg#dm-icons") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "dm-icons" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "dm-icons" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

